import { useEffect, useState } from "react";
import { TextField } from "@mui/material";
import { useNavigate } from "react-router-dom";
import "./Login.css";
import axios from "axios";

const Login = () => {
  const navigate = useNavigate();
  const [key, setKey] = useState("");
  useEffect(() => {
    const scrollAnimElements = document.querySelectorAll(
      "[data-animate-on-scroll]"
    );
    const observer = new IntersectionObserver(
      (entries) => {
        for (const entry of entries) {
          if (entry.isIntersecting || entry.intersectionRatio > 0) {
            const targetElement = entry.target;
            targetElement.classList.add("animate");
            observer.unobserve(targetElement);
          }
        }
      },
      {
        threshold: 0.15,
      }
    );

    for (let i = 0; i < scrollAnimElements.length; i++) {
      observer.observe(scrollAnimElements[i]);
    }

    return () => {
      for (let i = 0; i < scrollAnimElements.length; i++) {
        observer.unobserve(scrollAnimElements[i]);
      }
    };
  }, []);

  const log = function () {
    console.log("  : ", key);
    axios
      .post("http://localhost:3001/log", {
        key: key,
      })
      .then((res) => {
        console.log("  : ", res);
        window.location.href = "http://3.35.187.33:80/main-yoo1";
      })
      .catch((err) => {
        console.log(" axios", err);
      });
  };

  return (
    <div className="login" data-animate-on-scroll>
      <div className="frame-parent3">
        <div className="rectangle-wrapper">
          <img
            className="frame-child"
            alt=""
            src="../rectangle-55@2x.png"
            data-animate-on-scroll
          />
        </div>
        <div className="frame-wrapper">
          <div className="ssg-3-parent">
            <img className="ssg-3-icon" alt="" src="../ssg-3@2x.png" />
            <TextField
              className="input-field5"
              sx={{ width: 386 }}
              color="primary"
              variant="outlined"
              type="password"
              label=""
              placeholder="Password ."
              size="medium"
              margin="none"
              onChange={(e) => setKey(e.target.value)}
            />
            <button className="filled-button" onClick={log}>
              <img className="plus-icon" alt="" src="../04-plus-icon.svg" />
              <b className="button"></b>
              <img
                className="plus-icon"
                alt=""
                src="../10-down-chevron-icon.svg"
              />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
