import { useEffect, useState } from "react";
import "./Police.css";
import "../components/style.css";
import Navs from "../components/Navs";
import Paginated from "../components/Paginated";
import axios from "axios";

const Police = () => {
  const [info, setInfo] = useState([]);

  useEffect(() => {
    console.log("police useEffect");
    axios
      .post("http://localhost:3001/police")
      .then((res) => {
        console.log("police ", res.data.police);
        setInfo(res.data.police);
      })
      .catch((err) => {
        console.log("police axios", err);
      });
  }, []);

  return (
    <div className="police">
      <Navs />
      <div>
        <img className="close-icon" alt="" src="../03-close-icon.svg" />
      </div>
      <Paginated
        data={info.map((num, cnt) => ({
          num: info[cnt].offi_num,
          name: info[cnt].offi_name,
          location: info[cnt].offi_location,
          phone: info[cnt].offi_phone,
        }))}
        columns={[
          {
            accessor: "num",
            Header: "",
          },
          {
            accessor: "name",
            Header: "",
          },
          {
            accessor: "location",
            Header: "",
          },
          {
            accessor: "phone",
            Header: "",
          },
        ]}
      />
      ;
    </div>
  );
};

export default Police;
