import "./Error1.css";
import "../components/style.css";
import Navs from "../components/Navs";
import Paginated from "../components/Paginated";
import { useState, useEffect } from "react";
import axios from "axios";

const Error1 = () => {
  const [errorValue, setError] = useState([]);
  const name = sessionStorage.getItem("name");

  useEffect(() => {
    console.log(" useEffect");
    axios
      .post("http://localhost:3001/error", {
        name: name,
      })
      .then((res) => {
        console.log(" ", res.data.error);
        setError(res.data.error);
      })
      .catch((err) => {
        console.log(" axios", err);
      });
  }, []);

  return (
    <div className="error1">
      <Navs />
      <div>
        <img className="close-icon" alt="" src="../03-close-icon.svg" />
      </div>
      <Paginated
        data={errorValue.map((num, cnt) => ({
          num: errorValue[cnt].num,
          error: errorValue[cnt].error,
          light: errorValue[cnt].tl_error,
          date: errorValue[cnt].date.substring(0, 10),
        }))}
        columns={[
          {
            accessor: "num",
            Header: "",
          },
          {
            accessor: "error",
            Header: "",
          },
          {
            accessor: "light",
            Header: "",
          },
          {
            accessor: "date",
            Header: "",
          },
        ]}
      />
      ;
    </div>
  );
};

export default Error1;
